<template>
    <div id="main-scrollbar">
        <section-intro @introState="onIntroState"></section-intro>
        <section-hello :scroller="this.scroller"></section-hello>
        <section-works :scroller="this.scroller"></section-works>
        <section-awards :scroller="this.scroller" @scrollState="onScrollState"></section-awards>
        <section-skills :scroller="this.scroller" @scrollState="onScrollState"></section-skills>
        <section-contact></section-contact>
        <section-sideproject></section-sideproject>
    </div>
</template>

<script>
import { onMounted, getCurrentInstance } from '@vue/runtime-core'
import Scrollbar from 'smooth-scrollbar';
import SectionIntro from '@/components/Section-Intro.vue'
import SectionHello from '@/components/Section-Hello.vue'
import SectionWorks from '@/components/Section-Works.vue'
import SectionAwards from '@/components/Section-Awards.vue'
import SectionSkills from '@/components/Section-Skills.vue'
import SectionContact from '@/components/Section-Contact.vue'
import SectionSideproject from '@/components/Section-Sideproject.vue'
import gsap from 'gsap'
import ScrollTrigger from "gsap/ScrollTrigger"

export default {
    name: 'HomePage',
    components: {
        SectionIntro,
        SectionHello,
        SectionWorks,
        SectionAwards,
        SectionSkills,
        SectionContact,
        SectionSideproject
    },
    data() {
        return {
            endIntro: false,
            state: {
                count: 0,
                awards: false,
                skills: false,
            },
            scroller: Object,
        }
    },
    methods: {
        // If Intro has loaded
        onIntroState(state) {
            this.endIntro = state;
        },
        // Stan by scrollTrigger with 'pin' in child components
        onScrollState(state){            
            if(state){
                this.state.count++;
            }
            if(this.state.count === 2){
                this.backgroundChanger();
            }
        },
        // Background Color Animation
        backgroundChanger(){
            let backgrounds = { light: '#fafafa', apricot: '#FFBBAA', white: '#fffcee', lemon: '#F9FF95', blue: '#7065ff', melon: '#D5FFA4', orange: '#ffd378' };

            gsap.utils.toArray('.section').forEach((section, index) => {
                gsap.to(this.scroller, {
                    backgroundColor: Object.values(backgrounds)[index],
                    immediateRender: false,
                    duration: 0.2,
                    scrollTrigger: {
                        trigger: section,
                        scroller: this.scroller,
                        start: 'top center',
                        end: () => `+=${section.offsetHeight}`,
                        toggleActions: 'play none none reverse',
                        toggleClass: {
                            targets: '#main-scrollbar',
                            className: 'theme-' + Object.keys(backgrounds)[index]
                        }
                    }
                });
            });
        }
    },
    setup(props, { emit }) {
        onMounted(() => {
            gsap.registerPlugin(ScrollTrigger);

            // Sync Smooth Scroll & GSAP
            const mainScrollbar = document.querySelector('#main-scrollbar');
            let scrollbar = Scrollbar.init(mainScrollbar, { damping: 0.07, delegateTo: document });

            scrollbar.setPosition(0, 0);
            scrollbar.track.xAxis.element.remove();

            ScrollTrigger.scrollerProxy(mainScrollbar, {
                scrollTop(value) {
                    if (arguments.length) {
                        scrollbar.scrollTop = value;
                    }
                    return scrollbar.scrollTop;
                }
            });

            scrollbar.addListener(ScrollTrigger.update);

            getCurrentInstance().data.scroller = mainScrollbar;

            // This part is only necessary if you're using ScrollTrigger's markers:
            // if (document.querySelector('.gsap-marker-scroller-start')) {
            //     const markers = gsap.utils.toArray('[class *= "gsap-marker"]');
            //     scrollbar.addListener(({ offset }) => gsap.set(markers, { marginTop: -offset.y }));
            // }
            // End section necessary only if you're using ScrollTrigger's markers


            // Custom Cursor
            const pointerElements = document.querySelectorAll('button, a, input, textarea, [data-skill-name]');

            let cursor = "";
            let mouse = false;

            window.addEventListener("mousemove", function (e) {
                const mouseX = e.clientX;
                const mouseY = e.clientY;

                if (!mouse) {
                    let m = document.createElement("div");
                    m.classList.add("cursor");
                    document.querySelector("body").appendChild(m);

                    cursor = m;
                    mouse = true;
                }

                gsap.set(cursor, {
                    left: mouseX,
                    top: mouseY,
                });
            });

            pointerElements.forEach(button => {
                button.addEventListener("mouseleave", () => {
                    cursor.removeAttribute("data-cursor");
                });
                button.addEventListener("mousemove", () => {
                    cursor.setAttribute('data-cursor', 'active');
                });
            });
            
            return {
                // To Parent
                scroll: emit("sendScrollbar", scrollbar)
            }
        })
    }
}
</script>
<style lang="scss">
.cursor {
    z-index: 100;
    position: fixed;
    width: 20px;
    height: 20px;
    background-color: $color_primary;
    border: 2px solid $color_dark;
    border-radius: 50%;
    margin: -10px 0 0 -10px;
    transition: transform .4s cubic-bezier(0.51, 0.24, 0.16, 0.94);
    transform-origin: center center;
    user-select: none;
    pointer-events: none;

    &[data-cursor="active"] {
        transform: scale(0.5);
        opacity: 0.8;
        background-color: $color_dark;
        border-color: $color_primary;
    }
}
#main-scrollbar{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
[data-scrollbar]{
    .scrollbar-track{
        background: transparent;
    }
    .scrollbar-thumb{ 
        border-radius: 0;
        transition: background .5s ease;
        background: $color_primary;
    }
    &.theme-apricot .scrollbar-thumb {
        background: $color_primary;
    }
    &.theme-white .scrollbar-thumb {
        background: $color_secondary;
    }
    &.theme-lemon .scrollbar-thumb {
        background: $color_secondary;
    }
    &.theme-blue .scrollbar-thumb {
        background: $color_secondary;
    }
    &.theme-melon .scrollbar-thumb {
        background: $color_primary;
    }
}
.section{
    overflow: hidden;
    padding: 50vh 0;
    min-height: 100vh;

    &--intro{
        padding-top: 0;
    }
    &--sideproject{
        padding-bottom: 20vh;
    }
}
@media only screen and (max-width: 767px){
    .cursor {
        display: none;
    }
    [data-scrollbar] {
        .scrollbar-track-y,
        .scrollbar-thumb {
            width: 6px;
        }
    }
}
</style>
