            <div class="hello__description">
                <p>안녕하세요. 퍼블리셔 우상민입니다 :) <br>이니셜로 만든 로고를 사용하여 사이트를 만들었습니다.</p>
                <p>저는 지난 4년 동안 에이전시에서 다양한 퍼블 프로젝트를 담당했습니다. <br>반응형 홈페이지, 웹접근성 마크업, 하이브리드 웹 퍼블 뿐만 아니라 Vue.js 에서도 여러
                    프로젝트를 진행했습니다.</p>
                <p>요즘은 트렌디한 애니메이션을 위해 GSAP을 공부하고 있습니다. <br>그리고 항상 새로운 CSS 기능도 찾아다니고 있습니다. <br>평소에 음악을 듣는 것을 좋아해서 Vue.js 공부를 위해 사이드 프로젝트로 간단한 음악 게임을 만들고 있습니다. </p>
                <p>끝없이 음악을 탐색하는 '트렌드 세터'로서 플레이리스트에 차곡차곡 추가해나가고 있으며, 걷는걸 좋아해서 일주일에 2번 이상 6km씩 걷습니다.</p>
