<script>
import { onMounted, watch } from 'vue';
import gsap from 'gsap'
import ScrollTrigger from "gsap/ScrollTrigger"

export default {
    props: ['scroller'],
    setup(props) {
        onMounted(() => {
            gsap.registerPlugin(ScrollTrigger);

            // When Scroller Loaded
            watch(() => props.scroller, () => {    
                gsap.fromTo('.hello__description p', {
                    opacity: 0,
                    x: '-10%'
                }, {
                    opacity: 1,
                    x: '0%',
                    duration: 0.8,
                    stagger: 0.2,
                    scrollTrigger: {
                        trigger: '.hello__description',
                        scroller: props.scroller,
                        start: "top center",
                        end: '=+100%',
                        toggleActions: 'play none none reverse'
                    }
                });
            });
        });

    }
}
</script>
<style lang="scss" scoped>
@keyframes wave{
    from{
        x: -100px
    }
    to{
        x: 0;
    }
}
@keyframes fill-up-1{
    from{
        y: 13px;
        height: 0;
    }
    to{
        y: -3px;
        height: 15px;
    }
}
@keyframes fill-up-2{
    from{
        y: 25px;
        height: 0;
    }
    to{
        y: 9px;
        height: 15px;
    }
}
@keyframes fill-up-3{
    from{
        y: 37px;
        height: 0;
    }
    to{
        y: 21px;
        height: 15px;
    }
}
@keyframes fill-up-4{
    from{
        y: 49px;
        height: 0;
    }
    to{
        y: 33px;
        height: 15px;
    }
}
[data-water-fill]{
    animation-timing-function: linear, ease-out;
    animation-iteration-count: infinite, 1;
    pointer-events: none;
}
.theme-apricot [data-water-fill="1"]{
    animation-name: wave, fill-up-1;
    animation-duration: .8s, 2s;
}
.theme-apricot [data-water-fill="2"]{
    animation-name: wave, fill-up-2;
    animation-duration: 0.8s, 3s;
}
.theme-apricot [data-water-fill="3"]{
    animation-name: wave, fill-up-3;
    animation-duration: 0.9s, 3.5s;
}
.theme-apricot [data-water-fill="4"]{
    animation-name: wave, fill-up-4;
    animation-duration: 1s, 5s;
}
.hello{
    display: flex;
    margin: auto;
    padding: 0 50px;
    max-width: 1400px;
    transform: rotate(-10deg);

    &__title{
        flex: 0 1 60%;
        display: flex;
        justify-content: flex-end;
    }
    &__description{
        flex: 0 1 40%;
        margin-top: 19%;
        margin-left: -12%;
        max-width: 40%;
        line-height: 1.8;
        font-size: 1.8rem;

        p{
            margin-bottom: 30px;
            margin-left: -10%;
        }
        p:first-child{
            margin-left: 0;
            font-family: $font_secondary;
            font-size: 2.4rem;
            font-weight: bold;
        }
    }
}
.typography {
    position: relative;

    .h1 {
        display: inline-block;
        line-height: 1;
        font-family: $font_secondary;
        font-size: clamp(5rem, 15vw, 12rem);
        color: transparent;
    }
    .svg {
        z-index: -1;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
    }
}
// Responsive
@media only screen and (max-width: 1400px) {
    .hello{
        &__description {
            margin-top: calc(10% + 150px);
        }
    }
}
@media only screen and (max-width: 1023px) {
    .hello {
        flex-direction: column;
        align-items: flex-start;
        padding: 0 15px;
        max-width: 80vw;

        &__description {
            margin-top: 0;
            margin-left: 0;
            max-width: 100%;
            font-size: 1.6rem;

            p{
                margin-bottom: 20px;
                margin-left: 0;
            }
            p:first-child{
                margin-top: 20px;
                font-size: 2rem;
            }
        }
    }
}
@media only screen and (max-width: 767px) {
    .hello {
        transform: rotate(-5deg);
    }
}
</style>
