<template>
    <section class="section section--works">
        <div class="works">
            <div class="works-list">
                <div v-for="(work, index) in projectLists" v-bind:key="(work, index)" class="works-list__item" data-work>
                    <a class="works-list__link" :href="work.link" target="_blank">
                        <div class="works-list__text">
                            <p class="works-list__text-title">{{ work.title }}<small>({{ (index + 1) < 10 ? '0' + (index + 1) : (index + 1) }})</small></p>
                            <p class="works-list__text-year">({{ work.year }})</p>
                        </div>
                        <p class="works-list__contribution">{{ 'Main ' + work.contribution.main + '% / Sub ' + work.contribution.sub + '%' }}</p>
                    </a>
                    <div class="hover-reveal">
                        <div class="hover-reveal__image" v-for="h in hoverRevel" v-bind:key="h" :style="{'background-image': 'url(' + require('@/assets/images/works/' + work.image) + ')'}"></div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import { onMounted, watch } from '@vue/runtime-core'
import projects from "@/assets/data/projects.json"
import gsap from 'gsap'
import ScrollTrigger from "gsap/ScrollTrigger"

export default {
    props: ['scroller'],
    data(){
        return{
            projectLists: projects
        }
    },
    setup(props) {
        onMounted(() => {
            gsap.registerPlugin(ScrollTrigger);

            const works = gsap.utils.toArray('[data-work]');
            const timeline = [];

            // Get Mouse Position
            function getPosition(el){
                let rect = el.target.getBoundingClientRect();
                let mouseX = el.clientX - rect.left;
                let mouseY = el.clientY - rect.top;

                return{
                    'mouseX': mouseX,
                    'mouseY': mouseY
                }
            }
            
            works.forEach((work, index) => {
                const target = work.querySelector('.hover-reveal');
                const images = target.querySelectorAll('.hover-reveal__image');
                // Slicing without main image
                const afterimage = Array.from(images).slice(0, 4);
                let count = 1;

                timeline[index] = gsap.timeline();
                for(let i = afterimage.length - 1; i >= 0; i--){
                    timeline[index].set(afterimage[i], { x: (count * 5) + '%', y: (count * 10) + '%', opacity: 0.8 });
                    count++;
                }
                timeline[index].set(images[4], { x: '15%', y: '30%' });
                timeline[index].to(images[4], { duration: 0.2, x: 0, y: 0, ease: 'steps(3)' });
                timeline[index].to(afterimage, { duration: 0.4, opacity: 0, stagger: 0.1, ease: 'expo.out' }, '=-0.2');

                // When mouse into target
                work.addEventListener('mouseenter', (e) => {
                    gsap.set(e.currentTarget.querySelector('.hover-reveal'), { x: getPosition(e).mouseX, y: getPosition(e).mouseY });
                    gsap.set(target, { opacity: 1 });

                    timeline[index].restart(true, false);
                });

                // When mouse outto target
                work.addEventListener('mouseleave', () => {
                    gsap.to(target, { duration: 0.1, opacity: 0 });
                });

                // Image reveal & following while hovering on target
                work.addEventListener('mousemove', (e) => {                    
                    gsap.to(e.currentTarget.querySelector('.hover-reveal'), { duration: 0.8, ease: 'power2.out', x: getPosition(e).mouseX, y: getPosition(e).mouseY });
                });
            });

            // When Scroller Loaded
            watch(() => props.scroller, () => {
                // List fade in when scrolled
                works.forEach(work => {
                    gsap.fromTo(work, {
                        opacity: 0,
                    }, {
                        opacity: 1,
                        duration: 1,
                        scrollTrigger: {
                            trigger: work,
                            scroller: props.scroller,
                            start: "top 90%"
                        }
                    });
                });
            })
        });

        return{
            hoverRevel: new Array(5)
        }
    },
}
</script>
<style lang="scss" scoped>
.hover-reveal{
    opacity: 0;
    position: absolute;
    top: -300px;
    left: 50px;
    width: 500px;
    height: 300px;
    pointer-events: none;

    &__image{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: 50% 50%;
    }
}
.works{
    padding: 0 50px;
}
.works-list{
    $c: &;

    &__item{
        position: relative;
    }
    &__link:hover{
        color: $color_secondary;

        #{c}__year, p, small{
            z-index: 3;
        }
        & + .hover-reveal{
            z-index: 2;
        }
    }
    &__text{
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        transition: color .1s ease;
        
        &-title{
            z-index: 1;
            position: relative;
            line-height: 1.2;
            font-family: $font_secondary;
            font-size: clamp(1.8rem, 6vw, 10rem);
            font-weight: 500;
        }
        &-title small{
            z-index: 1;
            position: relative;
            margin-left: 1vw;
            font-size: clamp(1.2rem, 2vw, 2.2rem);
            opacity: 0.5;
            pointer-events: none;
        }
        &-year{
            z-index: 1;
            position: relative;
            margin-bottom: 1vw;
            font-family: $font_secondary;
            font-size: clamp(1.4rem, 2vw, 2.6rem);
            font-weight: 500;
            pointer-events: none;
        }
    }
    &__contribution{
        position: relative;
        padding-left: 0.5vw;
        padding-bottom: 1.7vw;
        width: 100%;
        font-family: $font_secondary;
        font-size: clamp(1.2rem, 2vw, 2.2rem);
        opacity: 0.5;
        pointer-events: none;
    }
}

// Responsive
@media only screen and (max-width: 767px) {
    .hover-reveal{
        display: none;
    }
    .works {
        padding: 0 20px;
    }
    .works-list {
        &__item {
            padding-bottom: 15px;
        }
    }
}
</style>
