<template>
    <section class="section section--awards">
        <div class="awards">
            <div class="awards__horizontal">
                <div class="award-list">
                    <div v-for="award in awardLists" v-bind:key="award" class="award-list__item">
                        <p class="award-list__item-year">{{ award.year }}</p>
                        <p class="award-list__item-project">{{ award.project }}</p>
                        <p class="award-list__item-content">
                            <strong>{{ award.organ }}</strong>
                            <strong>({{ award.type }})</strong>
                            {{ award.title }}
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import { onMounted, watch } from 'vue';
import awards from "@/assets/data/awards.json"
import gsap from 'gsap'
import ScrollTrigger from "gsap/ScrollTrigger"

export default {
    props: ['scroller'],
    data() {
        return {
            awardLists: awards
        }
    },
    setup(props, {emit}){
        onMounted(() => {
            gsap.registerPlugin(ScrollTrigger);

            const pinWrap = document.querySelector('.awards__horizontal');
            const animationWrap = document.querySelector('.awards__horizontal');
            let getToValue = () => -(animationWrap.scrollWidth - window.innerWidth);

            // When Scroller Loaded
            watch(() => props.scroller, () => {
                gsap.fromTo(animationWrap, {
                    x: 0
                }, {
                    x: () => getToValue(),
                    ease: "none",
                    scrollTrigger: {
                        trigger: '.awards',
                        scroller: props.scroller,
                        pinType: 'transform',
                        start: "80vh center",
                        end: () => "+=" + animationWrap.scrollWidth,
                        pin: pinWrap,
                        invalidateOnRefresh: true,
                        anticipatePin: 1,
                        scrub: true
                    }
                });

                emit('scrollState', true);
            });
        });
        return{}
    },
}
</script>
<style lang="scss" scoped>
.awards{
    transform: rotate(5deg);

    &__horizontal{
        display: flex;
    }
}
.award-list{
    display: flex;
    padding: 25px 100px 100px 100vw;

    &__item{
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        margin-left: 125px;
        padding: 50px;
        width: 350px;
        height: 200px;
        background-color: $color_white;

        &:hover &-project{
            -webkit-text-fill-color: transparent;
            -webkit-text-stroke: 2px $color_secondary;
        }
        &-year{
            position: absolute;
            top: -35px;
            right: -25px;
            font-family: $font_secondary;
            font-size: 5rem;
            font-weight: 900;
        }
        &-project{
            position: absolute;
            top: calc(100% - 25px);
            left: -25px;
            line-height: 1.1;
            font-family: $font_secondary;
            font-size: 5rem;
            color: $color_secondary;
        }
        &-content{
            text-align: center;
            font-size: 2rem;
    
            strong{
                display: block;
            }
        }
    }
}

// Responsive
@media only screen and (max-width: 1400px) {
    .award-list{
        &__item{
            margin-left: 105px;
            width: 280px;
            height: 160px;

            &-year{
                top: -25px;
                font-size: 3.6rem;
            }
            &-project{
                top: calc(100% - 20px);
                font-size: 3.4rem;
            }
            &-content{
                font-size: 1.6rem;
            }
        }
    }
}
</style>
