<template>
    <section class="section section--intro">
        <Transition appear @before-enter="beforeEnter" @enter="enter" :css="false">
            <div class="intro">
                <div class="intro__logo">
                    <svg v-for="i in 5" v-bind:key="i" :data-logo="i" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 365 100">
                        <g>
                            <polygon class="w1" points="132.5 0 232.5 0 132.5 100 132.5 0" />
                            <polygon class="w2" points="182.5 0 132.5 50 132.5 100 232.5 0 182.5 0" />
                        </g>
                        <g>
                            <polygon class="s1" points="182.5 0 232.5 0 182.5 50 132.5 50 182.5 0" />
                            <polygon class="s2" points="182.5 50 232.5 50 182.5 100 132.5 100 182.5 50" />
                        </g>
                        <g>
                            <polygon class="m1" points="182.5 100 232.5 50 232.5 0 132.5 100 182.5 100" />
                            <polygon class="m2" points="232.5 100 132.5 100 232.5 0 232.5 100" />
                        </g>
                    </svg>
                </div>
                <div class="intro__scroll">
                    <template v-for="(t, index) in scrollText" v-bind:key="(t, index)">
                        <div>{{t}}</div>{{ index === 5 ? ' ' : '' }}
                    </template>
                </div>
            </div>
        </Transition>
    </section>
</template>

<script>
import gsap from 'gsap'
export default {
    setup(props, {emit}) {
        const beforeEnter = (el) => {
            gsap.set(el, { opacity: 0 });
        }
        const enter = (el) => {
            const logoTimeline = gsap.timeline();
            const logoSet = {
                logo: document.querySelectorAll('[data-logo]'),
                color: ['#FF8DC3', '#FFBBAA', '#FFEC93', '#D5FFA4'],
                order: 0,
                duration: [1.5, 2, 0.4],
                distanceX: 2 * 1.5,
                distanceY: 7.3 * 1.5,
                point: {
                    w1: '0 0 100 0 0 100 0 0',
                    w2: '105 0 55 50 55 100 155 0 105 0',
                    s1: '180 0 230 0 177.5 52.5 127.5 52.5 180 0',
                    s2: '187.5 47.5 237.5 47.5 185 100 135 100 187.5 47.5',
                    m1: '260 100 310 50 310 0 210 100 260 100',
                    m2: '365 100 265 100 365 0 365 100'
                }
            }

            // 1. Reveal Logo
            logoTimeline.to(el, { ease: 'power1.in', opacity: 1, duration: 1 });
            
            // 2. Fill Layered Logo Color
            for (let i = logoSet.logo.length - 2; i >= 0; i--) {
                gsap.set(logoSet.logo[i], { fill: logoSet.color[logoSet.order] });

                logoSet.order++;
            }

            // 3. Divide To Logo
            logoTimeline.to('.w1', { duration: logoSet.duration[0], ease: 'expo.inOut', attr: { points: logoSet.point.w1 } });
            logoTimeline.to('.w2', { duration: logoSet.duration[0], ease: 'expo.inOut', attr: { points: logoSet.point.w2 } }, '-=' + logoSet.duration[0] +'');
            logoTimeline.to('.s1', { duration: logoSet.duration[0], ease: 'expo.inOut', attr: { points: logoSet.point.s1 } }, '-=' + logoSet.duration[0] +'');
            logoTimeline.to('.s2', { duration: logoSet.duration[0], ease: 'expo.inOut', attr: { points: logoSet.point.s2 } }, '-=' + logoSet.duration[0] +'');
            logoTimeline.to('.m1', { duration: logoSet.duration[0], ease: 'expo.inOut', attr: { points: logoSet.point.m1 } }, '-=' + logoSet.duration[0] +'');
            logoTimeline.to('.m2', { duration: logoSet.duration[0], ease: 'expo.inOut', attr: { points: logoSet.point.m2 } }, '-=' + logoSet.duration[0] +'');

            // 4. Slide The Layer
            logoTimeline.to(logoSet.logo[3], { x: logoSet.distanceX * 1 + '%', y: logoSet.distanceY * 1 + '%', duration: logoSet.duration[1], ease: 'expo.inOut' }, '-=1');
            logoTimeline.to(logoSet.logo[2], { x: logoSet.distanceX * 2 + '%', y: logoSet.distanceY * 2 + '%', duration: logoSet.duration[1], ease: 'expo.inOut' }, '-=1.94');
            logoTimeline.to(logoSet.logo[1], { x: logoSet.distanceX * 3 + '%', y: logoSet.distanceY * 3 + '%', duration: logoSet.duration[1], ease: 'expo.inOut' }, '-=1.94');
            logoTimeline.to(logoSet.logo[0], { x: logoSet.distanceX * 4 + '%', y: logoSet.distanceY * 4 + '%', duration: logoSet.duration[1], ease: 'expo.inOut' }, '-=1.94');

            // 5. Fade Away Layer
            logoTimeline.to(logoSet.logo[0], { opacity: 0, duration: logoSet.duration[2], ease: 'none' }, '-=0.3');
            logoTimeline.to(logoSet.logo[1], { opacity: 0, duration: logoSet.duration[2], ease: 'none' }, '-=0.3');
            logoTimeline.to(logoSet.logo[2], { opacity: 0, duration: logoSet.duration[2], ease: 'none' }, '-=0.3');
            logoTimeline.to(logoSet.logo[3], { opacity: 0, duration: logoSet.duration[2], ease: 'none' }, '-=0.3');

            // 6. Reveal Scroll Text
            logoTimeline.to('.intro__scroll div', { duration: 0.5, scaleY: 1, stagger: 0.1, ease: 'expo', onComplete() { emit('introState', true) } });
        }
        return {
            beforeEnter,
            enter,
            scrollText: ['s', 'c', 'r', 'o', 'l', 'l', 'd', 'o', 'w', 'n']
        }
    },
}
</script>

<style lang="scss" scoped>
.intro{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    padding: 0 50px;
    height: 100vh;
    // background-color: $color_light;

    &__logo{
        position: relative;
        max-width: 1000px;
        width: 100%;
    }
    &__logo [data-logo]{
        display: block;
        position: relative;
        width: 100%;
        height: 100%;
        fill: $color_primary;
    }
    &__logo [data-logo]:not(:first-child){
        position: absolute;
        top: 0;
        left: 0;
    }
    &__scroll{
        overflow: hidden;
        position: absolute;
        left: 50%;
        bottom: 15vh;
        transform: translateX(-50%);

        div{
            display: inline-block;
            text-transform: uppercase;
            font-size: 1.8rem;
            font-weight: 700;
            color: $color_primary;
            transform: scaleY(0);
            transform-origin: bottom;
        }
    }
}

// Responsive
@media only screen and (max-width: 767px) {
    .intro {
        &__scroll {
            div {
                font-size: 1.6rem;
            }
        }
    }
}
</style>
