<template>
    <section class="section section--skills">
        <div class="skills">
            <p class="skills__title">SKILLS</p>
            <transition @enter="onEnter" @leave="onLeave" :css="false">
                <div v-show="reveal" class="marquee-banner">
                    <div class="marquee-banner__text">
                        <span v-for="i in 40" v-bind:key="i">{{ marquee }}</span>
                    </div>
                </div>
            </transition>
            <ul class="skill-list">
                <li v-for="skill in skillLists" v-bind:key="skill" class="skill-list__item">
                    <img class="skill-list__icon" @mouseenter="animateMarquee" @mouseleave="animateMarquee" :data-skill-name="skill.name" :src="require('@/assets/images/skills/' + skill.src)" :alt="skill.name">
                </li>
            </ul>
        </div>
    </section>
</template>

<script>
import { onMounted, watch } from 'vue'
import skills from "@/assets/data/skills.json"
import gsap from 'gsap'
import ScrollTrigger from "gsap/ScrollTrigger"

export default {
    props: ['scroller'],
    data(){
        return{
            skillLists: skills,
            marquee: 'html5',
            reveal: false
        }
    },
    setup(props, {emit}) {
        onMounted(() => {
            gsap.registerPlugin(ScrollTrigger);
            
            watch(() => props.scroller, () => {
                gsap.to('.skills__title', {
                    scale: 1,
                    scrollTrigger: {
                        // pin: true,
                        trigger: '.skills',
                        scroller: props.scroller,
                        start: 'top top',
                        end: '=+100%',
                        scrub: 0.2
                    }
                });
                gsap.fromTo('.skill-list__icon', {
                    y: '50vh',
                    opacity: 0
                }, {
                    y: '0vh',
                    opacity: 1,
                    stagger: 0.1,
                    scrollTrigger: {
                        pin: true,
                        trigger: '.skills',
                        scroller: props.scroller,
                        start: 'top top',
                        end: '=+200%',
                        scrub: 0.2,
                    }
                });
    
                emit('scrollState', true);
            });
        });
        return{}
    },
    methods: {
        onEnter: function(el, done){
            gsap.to(el, { autoAlpha: 1, onComplete: done });
        },
        onLeave: function(el, done){
            gsap.to(el, { autoAlpha: 0, onComplete: done });
        },
        animateMarquee: function(el){
            if(el.type === 'mouseenter'){
                let name = el.target.getAttribute('data-skill-name');
                this.marquee = name;
                this.reveal = true;
            }else if(el.type === 'mouseleave'){
                this.reveal = false;
            }
        }        
    }
}
</script>
<style lang="scss" scoped>
@keyframes marguee-text {
    to{
        transform: translateX(-100vw);
    }
}
.marquee-banner{
    overflow: hidden;
    display: block !important;
    margin-left: -50px;
    margin-bottom: 50px;
    width: 100vw;
    background-color: $color_secondary;
    border-color: $color_dark;
    border-width: 4px 0px 4px 0px;
    border-style: solid;
    opacity: 0;
    visibility: hidden;

    &__text{
        display: flex;
        font-size: clamp(1.4rem, 2vw, 3rem);
        color: $color_lemon;
        font-weight: 700;
        text-transform: uppercase;
        animation: marguee-text 14s linear infinite;
        
        span{
            white-space: nowrap;
            padding-right: 5vw;
        }
    }
}
.skills{
    padding-top: 70px;
    padding-right: 50px;
    padding-left: 50px;

    &__title{
        text-align: center;
        font-family: $font_secondary;
        font-size: clamp(6rem, 16vw, 23.5rem);
        font-weight: 700;
        color: $color_light;
        transform: scale(2);
    }
}
.skill-list{
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 50px;
    margin: auto;
    max-width: 800px;
    width: 100%;
    text-align: center;

    &__item{
        height: 70px;
    }
    &__icon{
        width: auto;
        height: 100%;
    }
    &__icon:hover{
        filter: invert(1);
    }
}

// Responsive
@media only screen and (max-width: 1200px) {
    .skill-list {
        &__item {
            height: 60px;
        }
    }
}
@media only screen and (max-width: 767px) {
    .marquee-banner {
        margin-bottom: 30px;
        &__text {
            animation-duration: 8s;
        }
    }
    .skill-list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-row-gap: 30px;

        &__item {
            height: 40px;
        }
    }
}
</style>
