<template>
    <!-- 헤더 -->
    <main-header :scrollbar="this.scrollbar"></main-header>

    <!-- 메인 -->
    <router-view v-slot="{ Component, route }">
        <main :key="route.name" class="main">
            <component :is="Component" @sendScrollbar="onSendScrollbar"></component>
        </main>
    </router-view>
</template>

<script>
import MainHeader from '@/components/Main-Header.vue'

export default {
    name: 'App',
    components: {
        MainHeader
    },
    data() {
        return {
            scrollbar: Object
        }
    },
    methods: {
        onSendScrollbar(data){
            this.scrollbar = data;
        }
    },
    setup(){}
}
</script>

<style lang="scss">
*{
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}
html{
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    font-family: $font_primary;
    font-size: 62.5%;
    color: $color_dark;
    background-color: $color_light;
    word-break: keep-all;
    overflow-wrap: break-word;
    box-sizing: border-box;
    cursor: none;
}
body{
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    font-size: 1.6rem;
    line-height: 1.4;
}
a{
    color: inherit;
    text-decoration: none;
    cursor: none;
}
ul{
    list-style: none;
}
button{
    background: transparent;
    cursor: none;
    border: none;
}
input, textarea{
    cursor: none;
}
#wrap{
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
}
.main{
    width: 100%;
    height: 100%;
}
</style>