<template>
    <transition>
        <header class="header">
            <router-link aria-current-value="" active-class="" exact-active-class="" class="header__home" to="/">
                <img class="header__home-logo" src="assets/logo.svg" alt="">
            </router-link>
            <button class="header__menu">
                <span class="header__menu-line"></span>
                <span class="header__menu-line"></span>
                <span class="header__menu-line"></span>
            </button>
        </header>
    </transition>

    <div class="full-menu">
        <div v-for="i in 6" v-bind:key="i" :data-layer="i" class="full-menu__layer">
            <template v-if="i === 6">
                <nav class="full-menu__navigation">
                    <!-- Navigation Menu -->
                    <a v-for="link in links" v-bind:key="link" :data-nav-link="link" class="full-menu__navigation-link" href="javascript:"><span>{{ link }}</span></a>
                </nav>
                <div class="full-menu__logo">
                    <!-- Logo Under the Full Menu -->
                    <svg v-for="i in 5" v-bind:key="i" :data-full-logo="i" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 365 100">
                        <g>
                            <polygon class="w1" points="0 0 100 0 0 100 0 0" />
                            <polygon class="w2" points="105 0 55 50 55 100 155 0 105 0" />
                        </g>
                        <g>
                            <polygon class="s1" points="180 0 230 0 177.5 52.5 127.5 52.5 180 0" />
                            <polygon class="s2" points="187.5 47.5 237.5 47.5 185 100 135 100 187.5 47.5" />
                        </g>
                        <g>
                            <polygon class="m1" points="260 100 310 50 310 0 210 100 260 100" />
                            <polygon class="m2" points="365 100 265 100 365 0 365 10" />
                        </g>
                    </svg>
                </div>
                <button class="full-menu__close">
                    <svg class="full-menu__close-shape" viewBox="0 0 12 10" width="40px" height="40px">
                        <g class="bar1">
                            <path d="M11,10 L1,2"></path>
                            <path d="M11,2 L1,10"></path>
                        </g>
                        <g class="bar2">
                            <path d="M11,9 L1,1"></path>
                            <path d="M11,1 L1,9"></path>
                        </g>
                    </svg>
                </button>
            </template>
        </div>
    </div>
</template>
<script>
import { onMounted } from 'vue'
import gsap from 'gsap'
export default{
    props: ['scrollbar'],
    setup(props){
        onMounted(() => {
            const tl = gsap.timeline({ paused: true });
            const menuOpen = document.querySelector('.header__menu');
            const menuclose = document.querySelector('.full-menu__close');
            
            menuOpen.addEventListener('click', function(){
                tl.reversed(false);
            });
            menuclose.addEventListener('click', function(){
                tl.reversed(true);
            });

            tl.to('[data-layer]', { duration: 0.8, xPercent: -100, stagger: 0.04, ease: "expo.inOut" });
            tl.to('.full-menu__navigation-link', { duration: 0.3, opacity: 1, x: 0, stagger: 0.1, ease: "power1.out" }, 1);
            tl.to('.full-menu__close', { duration: 1, transform: 'scale(1, 1)', ease: 'expo.inOut' }, "=-1");
            tl.to('.full-menu__logo', { duration: 1, right: '4vw', opacity: 1, ease: 'expo.inOut' }, "=-.5");
            tl.to('.full-menu__logo [data-full-logo="1"]', { duration: 0.5, x: -4, y: -4, ease: 'expo' });
            tl.to('.full-menu__logo [data-full-logo="2"]', { duration: 0.5, x: -3, y: -3, ease: 'expo' }, "=-.5");
            tl.to('.full-menu__logo [data-full-logo="3"]', { duration: 0.5, x: -2, y: -2, ease: 'expo' }, "=-.5");
            tl.to('.full-menu__logo [data-full-logo="4"]', { duration: 0.5, x: -1, y: -1, ease: 'expo' }, "=-.5");

            tl.reverse();

            // Navigation Links
            const links = document.querySelectorAll('[data-nav-link]');
            const sections = document.getElementsByClassName('section');
            
            links.forEach((link, index) => {
                link.addEventListener('click', function(){
                    props.scrollbar.scrollTo(0, sections[index].offsetTop, 0);
                    tl.reversed(true);
                });
            });
        });

        return{
            links: ['home', 'hello', 'works', 'awards', 'skills', 'contact']
        }
    }
}
</script>
<style lang="scss">
.header{
    z-index: 100;
    position: sticky;
    top: 0;
    left: 0;

    &__home{
        display: block;
        position: absolute;
        top: 50px;
        left: 50px;

        &-logo{
            width: 146px;
            height: auto;
        }
    }
    &__menu{
        position: absolute;
        top: 50px;
        right: 50px;
        width: 30px;
        height: 30px;

        &-line{
            display: block;
            margin-bottom: 5px;
            width: 100%;
            height: 5px;
            background: $color_dark;
            transition: all .5s cubic-bezier(0.645, 0.045, 0.355, 1);
            transform-origin: left;
        }

        &:hover &-line:nth-child(2){
            transform: scaleX(70%);
        }
        &:hover &-line:nth-child(3){
            transform: scaleX(40%);
        }
    }
}
.full-menu{
    z-index: 101;
    position: fixed;
    top: 0;
    left: 100%;
    width: 100%;
    height: 100%;
    
    &__layer{
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        $colors: ($color_melon, $color_lemon, $color_apricot, $color-secondary, $color_primary, $color_dark);
        @each $color in $colors{
            $i: index($colors, $color);
            &:nth-child(#{$i}){
                background-color: #{$color};
            }
        }
    }
    &__close{
        position: absolute;
        top: 50px;
        right: 50px;
        transform: scale(1, 0);
        
        &-shape{
            display: block;
            fill: none;
            stroke: $color_melon;
            stroke-width: 2px;
        }
        &-shape .bar2{
            stroke: $color_primary;
        }
    }
    &__logo{
        position: absolute;
        right: 0vw;
        bottom: 50px;
        width: min(50vw, 400px);
        opacity: 0;

        [data-full-logo]{
            z-index: 5;
            position: relative;
            display: block;
            width: 100%;
            height: 100%;
            fill: $color_primary;
        }
        [data-full-logo]:not(:first-child){
            position: absolute;
            top: 0;
            left: 0;
            fill: $color_melon;
        }
        [data-full-logo="2"]{
            z-index: 4;
        }
        [data-full-logo="3"]{
            z-index: 3;
        }
        [data-full-logo="4"]{
            z-index: 2;
        }
        [data-full-logo="5"]{
            z-index: 1;
        }
    }
    &__navigation{
        display: flex;
        flex-direction: column;
        margin: auto;
        padding: 90px 20px 20px;
        max-width: 1400px;
        width: 100%;
        height: 100%;

        &-item{
            display: block;
            padding: 10px 0;
        }
        &-link{
            margin-right: auto;
            padding-top: 2px;
            padding-bottom: 2px;
            font-family: $font_secondary;
            font-size: clamp(3.6rem, 8vw, 7.6rem);
            color: $color_primary;
            text-transform: uppercase;
            transform: translateX(-10%);
            opacity: 0;
        }

        &-link span{
            display: block;
            transition: all .2s cubic-bezier(.175, .885, .32, 1.275);
        }
    }
}

@media (pointer: fine){
    .full-menu {
        &__navigation-link:hover span,
        &__navigation-link:focus span {
            transform: translateX(-15px) translateY(-15px);

            $value: ();

            @for $i from 1 through 15 {
                $num: ($i * 0.1)+rem;
                $color: $color_melon;
                $theShadow: $num $num $color;
                $value: append($value, $theShadow, comma)
            }

            text-shadow: $value;
        }
    }
}

// Responsive
@media only screen and (max-width: 767px) {
    .header{
        &__home{
            top: 20px;
            left: 20px;
            
            &-logo{
                width: 102px;
            }
        }
        &__menu{
            top: 20px;
            right: 20px;
        }
    }
    .full-menu {
        &__close {
            top: 20px;
            right: 20px;
        }
    }
}
</style>